import React from "react";
import styled from "styled-components";

const Stepper = ({ className, step }) => {
  const stepTitle = ["Nhập thông tin", "Xem demo báo cáo", "Nhập mã kích hoạt"];
  return (
    <div className={`${className} step__nav d-flex justify-content-center`}>
      {stepTitle.map((title, idx) => (
        <div key={idx} className={`step ${step === idx ? "active" : ""}`}>
          <span>{idx + 1}</span> {title}
        </div>
      ))}
    </div>
  );
};

export default styled(Stepper)`
  &.step__nav {
    margin: 40px 0;
    .step {
      margin: 0 20px;
      &:hover {
        cursor: pointer;
      }
      > span {
        place-items: center;
        padding: 4px 10px;
        border-radius: 50%;
        border: 3px solid;
      }
    }
    .active {
      font-weight: bolder;
      color: ${(props) => props.theme.colorTextTitle};
    }
  }
`;
