import React, { lazy, Suspense, useState } from "react";
import { useForm } from "react-hook-form";
import styled from "styled-components";
import useActiveCode from "../../common/hooks/useActiveCode";
import Stepper from "./Stepper";
import HashLoader from "react-spinners/HashLoader";
import { useHistory } from "react-router";
import useGetNumberResult from "../../common/hooks/useGetNumberResult";
const Form1 = lazy(() => import("./Form1"));
const Form2 = lazy(() => import("./Form2"));
const Form3 = lazy(() => import("./Form3"));

const GettingReport = ({ className }) => {
  const history = useHistory();
  const {
    data: numbers = [],
    mutateAsync: getNumbers,
    isLoading: getNumberLoading,
  } = useGetNumberResult();
  const {
    mutate: activeCode,
    error: activeCodeError,
    isLoading: activeCodeLoading,
  } = useActiveCode();
  const isLoading = getNumberLoading || activeCodeLoading;
  const { register, handleSubmit, errors } = useForm();
  const [step, setStep] = useState(0);
  const form = [
    <Form1 register={register} errors={errors} isLoading={isLoading} />,
    <Form2 numbers={numbers} />,
    <Form3
      register={register}
      errors={errors}
      activeCodeError={activeCodeError}
      isLoading={isLoading}
    />,
  ];
  const getForm = (step) => {
    return form[step];
  };
  const handleMultiform = async (data) => {
    try {
      switch (step) {
        case 0:
          console.log(data);
          await getNumbers(data);
          // console.log(formData);
          break;
        case 2:
          activeCode(data.code, {
            onSuccess: () => {
              history.push("/user/pdf");
            },
          });
          break;
        default:
      }
      next();
    } catch (error) {}
  };

  const next = () => {
    if (step < form.length - 1) setStep((prev) => ++prev);
  };
  return (
    <section className={`${className} container step_by_step`}>
      <form onSubmit={handleSubmit(handleMultiform)}>
        <main className="step__form my-3">
          <Stepper step={step} />
          <Suspense
            fallback={
              <div id="loader-wrapper">
                <div id="loader"></div>
              </div>
            }
          >
            {getForm(step)}
          </Suspense>

          <section className="step__form__button mt-3">
            <button
              type="button"
              className={`prev step__from__button button-secondary--outline ${
                step <= 0 ? "button-disable" : ""
              }`}
              onClick={() => setStep((prev) => Math.max(0, prev - 1))}
            >
              <span style={{ marginRight: 10 }}>
                <i className="fas fa-long-arrow-alt-left"></i>
              </span>
              Trở về
            </button>
            <button
              type="submit"
              className={`next step__from__button button-secondary ${isLoading ? "disabled" : ""}`}
              // onClick={step < form.length - 1 ? next : null}
            >
              {isLoading ? (
                <HashLoader color="#fafafa" size={24} loading={true} />
              ) : step === 2 ? (
                "Hoàn thành"
              ) : (
                "Tiếp theo"
              )}
              {!isLoading && step < 2 && (
                <span style={{ marginLeft: 10 }}>
                  <i className="fas fa-long-arrow-alt-right"></i>
                </span>
              )}
            </button>
          </section>
        </main>
      </form>
    </section>
  );
};

export default styled(GettingReport)`
  .step__form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__button {
      -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
      -khtml-user-select: none; /* Konqueror HTML */
      -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
      user-select: none;
      display: flex;
      justify-content: space-between;
      > button {
        padding: 10px 15px;
        border: 0;
        font-weight: bolder;
        min-width: 126px;
        &.button-disable {
          visibility: hidden;
        }
        /* &:nth-child(1) {
          background-color: rgba(255, 255, 255, 0);
          &:hover {
            background-color: rgba(255, 255, 255, 0.385);
          }
        }
        &:nth-child(2) {
          color: #000;
          &:hover {
            background-color: #ffc267d7;
          }
        } */
      }
    }
  }
`;
